#pedals {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 20px;
}

.pedal {
  width: 100px;
  height: 40px;
  line-height: 40px;
  border-radius: 10px;
}

.pedal.active {
  background: rgba(214, 78,  18,  1.0);
}

#scaleNotes, #scaleQualities {
  display: flex;
  gap: 2px;
  justify-content: center;
  margin-top: 20px;
}

.scale {
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 10px;
}

#scaleQualities .scale {
  width: auto;
  padding: 0px 10px;
}

#board .scale.active,
#board.scale-0-0 .scale.scale-0,
